
.splashscreen {
  width: auto;
  height: 50vh;
  position: relative;
  overflow: hidden;
}
.splashscreen:after {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    content: '';
    background-color: rgba(44, 62, 80, 0.75);
}
.splashscreen .splashscreen-item {
    width: 50%;
    position: relative;
    -o-object-fit: cover;
       object-fit: cover;
    height: 100%;
    float: left;
}
.splashscreen .splashscreen-text {
    position: absolute;
    left: 0;
    right: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    text-align: center;
    color: white;
    text-align: center;
    z-index: 99;
    bottom: 50%;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}
.splashscreen .splashscreen-text p {
      display: inline-block;
      width: 100%;
      margin: 0;
      margin-bottom: 20px;
      text-align: center;
      font-size: 5em;
      font-weight: 100;
}
.splashscreen-img {
  width: 100%;
  height: auto;
  -o-object-fit: cover;
     object-fit: cover;
}
.play-button {
  padding: 0 50px;
  font-weight: bold;
  line-height: 60px;
  height: 60px;
  background: none;
  border: 3px solid white;
}
.play-button:hover {
    background: white;
    color: #CF142B;
}
.container {
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  padding: 20px;
  background: white;
}
.container h2 {
    font-weight: 100;
    color: #CF142B;
}



